<script>

export default {

    props: {
        visitId: {
            type: Number,
            required: true,
        },
        invoice: {
            type: Object,
            required: true,
        },
        customer: {
            type: Object,
            required: true,
        },
        items: {
            type: Array,
            required: true,
        },
        stores: {
            type: Array,
            required: true,
        },
        doctors: {
            type: Array,
            default: () => [],
        },
    },

    data () {
        return {

            form: new window.Form({
                ledger_id: this.invoice.id,
                store_id: null,
                item: null,
                vendor_id: null,
                quantity: 1,
                unit_price: 0,
                date: new Date().toISOString().slice(0, 10),
                comments: '',
                items: [],
            }),

            showStore: false,
            showVendor: false,

            assignedTo: [],

            availableStock: 0,

            enableForm: false,
        }
    },

    computed: {
        selectedProduct () {
            if (!this.form.item) {
                return false
            }

            const vm = this

            return this.items.find((item) => item.id === vm.form.item.id)
        },

        formFilledCorrectly () {
            if (!this.form.item) {
                return false
            }

            if (this.form.item.item_type.is_inventory && !this.form.store_id) {
                return false
            }

            if (this.form.item.item_type.is_inventory && this.availableStock === 0) {
                return false
            }

            if (this.form.item && this.assignedTo.length > 0 && !this.form.vendor_id) {
                return false
            }

            return true
        },

        totalDraftBills () {
            if (!this.form.items.length) {
                return 0
            }

            return this.form.items.reduce((acc, current) => {
                return acc + current.unit_price * current.quantity
            }, 0)
        },
    },
    watch: {
        'form.item': function (item) {
            this.showStore = false
            this.showVendor = false
            this.assignedTo = []
            this.form.vendor_id = null
            this.form.store_id = null

            if (item) {
                this.setupProductPriceSetup(item)

                this.displayStoreSelection(item)

                this.displayDoctorsAssigned()
                this.findAvailableStock()
            }
        },
        'form.store_id': function (storeId) {
            if (storeId) {
                this.findAvailableStock()
            }
        },
    },

    created () {
        this.$bvModa.show('billing-modal')
    },

    methods: {
        toggleForm () {
            this.$bvModal.show('billing-modal')

            // this.enableForm = !this.enableForm
        },

        addBill () {
            if (!this.formFilledCorrectly) {
                this.$toast.error('Please fill the form correctly')

                return
            }
            this.form.items.push({
                item: this.form.item,
                item_id: this.form.item.id,
                quantity: this.form.quantity,
                unit_price: this.form.unit_price,
                vendor_id: this.form.vendor_id,
                store_id: this.form.store_id,
                description: this.form.item.name,
                instruction: null,
            })
        },
        removeBill (bill) {
            const index = this.form.items.indexOf(bill)
            //
            if (index > -1) {
                this.form.items.splice(index, 1)
            }
        },
        chargePatient () {
            if (!this.form.items.length) {
                this.$toast.error('Please select the product to be charged')
                return
            }

            this.form.post('/patient-bills')
                .then(() => {
                    this.$toast.success('The bill has been charged')

                    this.cleanUp()

                    this.$emit('bill-added')
                })
                .catch((error) => {
                    this.form.processing = false
                    this.form.errors.record(error.response.data.errors)
                })
        },
        displayStoreSelection (item) {
            if (item.item_type.is_inventory === true) {
                this.showStore = true

                const store = this.stores.find((store) => store.name === 'Pharmacy')

                this.form.store_id = store.id
            }
        },
        displayDoctorsAssigned () {
            const vm = this

            const selectedProduct = this.items.find(item => item.id === vm.form.item.id)

            if (selectedProduct && selectedProduct.assignments.length) {
                this.assignedTo = selectedProduct.assignments.map((assignment) => {
                    return assignment.vendor
                })

                if (this.assignedTo.length === 1) {
                    this.form.vendor_id = this.assignedTo[0].id
                }

                this.showVendor = true
            }
        },
        setupProductPriceSetup (item) {
            if (this.invoice.wallet.type === 0) {
                this.form.unit_price = item.cash_price.charge_value * 0.01
            }

            if (this.invoice.wallet.type === 1) {
                this.form.unit_price = item.scheme_price.charge_value * 0.01
            }
        },
        findAvailableStock () {
            this.availableStock = 0

            if (this.selectedProduct && this.form.store_id) {
                this.$httpClient.get('/available_stock/' + this.selectedProduct.id + '/' + this.form.store_id)
                    .then(({ data }) => {
                        this.availableStock = data
                    }).catch()
            }
        },

        cleanUp () {
            this.form.reset()

            this.showStore = false
            this.showVendor = false

            // this.items = []
            this.assignedTo = []
        },

    },
}
